import React from 'react';
import styled from 'styled-components';
import FacebookSVG from '../icons/facebook';
import TwitterSVG from '../icons/twitter';
import InstagramSVG from '../icons/instagram';

import {
    ContentRight,
    SocialMediaBox,
    SocialMediaButton
} from '../styles/blog_right_styled';

import {
    RibbonInfoHeader
} from '../styles/blog_panels'

import {
    BoxAd
} from '../styles/ads';

const SocialMediaWrapper = styled.div`
display: flex;
width: 100%;
height: 100px;
margin: 15px 0;
justify-content: space-between;
`;

const BlogRight = () => {
    return (
        <ContentRight>
            <RibbonInfoHeader customColor={"black"} title={"SPRAWDZ"} news={""}/>
            <SocialMediaWrapper>
                <SocialMediaBox customColor={"rgb(16,45,188)"} icon={<FacebookSVG />}/>
                <SocialMediaBox customColor={"rgb(4,243,255)"} icon={<TwitterSVG />}/>
                <SocialMediaBox customColor={"rgb(18,91,193)"} icon={<InstagramSVG />}/>
            </SocialMediaWrapper>
            <BoxAd customAdUrl={"https://static.wixstatic.com/media/d06953_d0000308ddd34913b036c0b1621a9828~mv2.jpg/v1/fill/w_750,h_422,al_c,q_90/d06953_d0000308ddd34913b036c0b1621a9828~mv2.jpg"}/>
            <SocialMediaWrapper>
                <SocialMediaButton title={"YouTube"}/>
                <SocialMediaButton title={"TikTok"}/>
                <SocialMediaButton title={"Instagram"}/>
                <SocialMediaButton title={"Twitter"}/>
                <SocialMediaButton title={"Esport"}/>
            </SocialMediaWrapper>
        </ContentRight>
    )
}

export default BlogRight;